.route-form {
  margin-bottom: 20px;

  text-align: center;
}

.route-form-section {
  padding: 40px 15px;
}

.container__route-form {
  border-radius: 15px;
  padding: 15px;

  background-color: $primary-black-alpha;
  color: $primary-white;
  backdrop-filter: blur(3px);
  box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.24), 0px 6px 6px rgba(0, 0, 0, 0.12),
    2px 6px 8px rgba(0, 0, 0, 0.32);
}

.route-form__title {
  margin-bottom: 12px;

  text-align: center;
  font-weight: 700;
  font-size: 20px;
}

.route-form__service {
  margin-bottom: 12px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.route-form__service-type {
  display: flex;
  padding: 5px;

  border-radius: 4px;
  fill: $primary-white;

  & input:checked + .route-form__service-type-wrapper {
    border-color: $secondary-brand-color;
    fill: $secondary-brand-color;
    color: $secondary-brand-color;
  }
}

.route-form__service-type-wrapper {
  border: 2px solid $primary-white;
  border-radius: 15px;
  padding: 10px;
  display: flex;
  align-items: center;
  gap: 5px;
}

.route-form__select,
.route-form__date {
  display: flex;
  flex-direction: column;
  text-align: start;
}

.route-form__city {
  margin-bottom: 4px;
}

.route-form__selector {
  height: 40px;
  width: 100%;
  margin-bottom: 10px;

  font-size: 18px;
  text-transform: uppercase;

  border-radius: 4px;
  color: $primary-black;
  background-color: $primary-white-alpha;
}

.route-form__selector--no-margin {
  margin-bottom: 0px;
}

.route-form__date {
  margin-bottom: 20px;
}

.route-form__btn {
  display: flex;
  margin: 0 auto;
  align-items: center;
  justify-content: center;
  gap: 10px;
  fill: $primary-white;
}

.routes__bus {
  text-align: start;
  border-radius: 15px;
  padding: 15px;

  color: $primary-white;
  background-color: rgba($color: #ffffff, $alpha: 0.4);
  &:not(:last-child) {
    margin-bottom: 20px;
  }
}

.routes__table {
  margin-bottom: 20px;
}

.routes__addons {
  display: flex;
  flex-wrap: wrap;
  gap: 5px;
}

.routes__footer {
  display: flex;
  justify-content: space-between;
}

.routes__choose-btn {
  display: flex;
  align-items: center;
  gap: 5px;
}
